*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.body{
    position: relative;
    font-family: 'Roboto', sans-serif;
    background-color: #f4f4f4;
    color: #252422;
}
.body--black{
    background-color: #252422;
    color: #f9cdcd;
}

.header{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 130px;
    .header__container{
        width: 90%;
        margin: 0 auto;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .header__logo{
        width: 160px;
        fill: #f4f4f4;
    }
    .header__logo--black{
        fill: #252422;
    }
    .header__logo--pink{
        fill: #f9cdcd;
    }
    .header__nav{
        display: flex;
        padding-right: 100px;
    }
    .header__nav-item{
        color: #f4f4f4;
        text-decoration: none;
        margin: 0 2rem;
        position: relative;
        &:after{
            height: 1px;
            position: absolute;
            content: '';
            bottom: 0;
            left: 0;
            background-color: #f4f4f4;
            width: 0;
            transition: all 0.35s ease;
        }
        &:hover:after{
            width: 100%;
        }
    }
    .header__nav-item--current:after{
        width: 100%;
    }
    .header__nav-item--black{
        color: black;
        &:after{
            background-color: #252422;
        }
    }
    .header__nav-item--pink{
        color: #f9cdcd;
        &:after{
            background-color: #f9cdcd;
        }
    }
    .header__menu{
        background: inherit;
        border: none;
        color: #f4f4f4;
        font-size: 1.3rem;
        cursor: pointer;
    }
    .header__menu--black{
        color: #252422;
    }
    .header__menu--pink{
        color: #f9cdcd;
    }
}


.accessibility{
    box-sizing: content-box;
    position: fixed;
    bottom: 20px;
    left: 20px;
    z-index: 2;
    background-color: #252422;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: none;
    padding: 8px;
    cursor: pointer;
    &:hover{
        transform: scale(1.1);
        transition: all 0.2s ease-in-out;
    }
    &:not(hover){
        transform: scale(1);
        transition: all 0.2s ease-in-out;
    }
    .accessibility__icon{
        width: 100%;
        height: 100%;
        fill: #f4f4f4;
    }
}


.hero{
    width: 100%;
    height: 100vh;
    .hero__video{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}


.awards{
    width: 100%;
    height: 70vh;
    .awards__container{
        border-bottom: #252422 solid 1px;
        width: 90%;
        height: 100%;
        margin: 0 auto;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    .awards__link{
        text-decoration: none;
        color: #252422;
        width: 33.3%;
        text-align: center;
        height: 40%;
        &:hover{
            .awards__logo{
                transform: scale(1.05);
                transition: all 0.3s ease-in-out;
            }
            .awards__title{
                text-decoration: underline #252422 2px;
            }
        }
        &:not(hover){
            .awards__logo{
                transform: scale(1);
                transition: all 0.3s ease-in-out;
            }
        }
    }
    .awards__logo{
        display: block;
        margin: 10% auto;
        width: 40%;
        height: 40%;
    }
    .awards__title{
        display: block;
        width: 40%;
        margin: 0 auto;
        line-height: 1.2rem;
        text-transform: uppercase;
        font-size: 0.8rem;
        font-weight: 500;
    }
}



.overview{
    width: 100%;
    height: 50vh;
    .overview__container{
        width: 90%;
        height: 100%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: start;
    }
    .overview__content{
        width: 50%;
        height: 100%;
        padding: 80px 0;
        padding-right: 80px;
    }
    .overview__link{
        display: inline-block;
        text-decoration: none;
        color: #252422;
        border: #252422 1px solid;
        border-radius: 20px;
        padding: 8px 30px;
        margin-top: 40px;
        font-size: 0.7rem;
        font-weight: 600;
        background-image: linear-gradient(to top, #252422 50%, #f4f4f4 50%);
        background-size: 100% 200%;
        &:hover{
            animation: button-animation 0.25s 1 ease-in-out forwards;
            
        }
        &:not(hover){
            animation: button-animation-off 0.25s 1 ease-in-out forwards;
        }
    }
    .overview__text{
        font-size: 2.2rem;
        font-weight: 500;
        line-height: 2.5rem;
    }
    .overview__image{
        width: 550px;
        height: 350px;
        padding-top: 80px;
    }
    .overview__image-svg{
        fill: #252422;
    }
}




.case-studies{
    width: 100%;
    height: calc(width);
    margin-bottom: 10em;
    .case-studies__container{
        width: 90%;
        height: 100%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: start;
    }
    .case-studies__card{
        width: 32%;
        height: 100%;
        text-decoration: none;
        color: #252422;
        text-transform: uppercase;
        &:hover .case-studies__title{
            text-decoration: #252422 underline 1.5px;
        }
        &:hover .case-studies__media{
            transform: scale(1.05);
            transition: all 0.2s ease-in-out;
        }
        &:not(hover) .case-studies__media{
            transform: scale(1);
            transition: all 0.2s ease-in-out;
        }
    }
    .case-studies__media-container{
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
    .case-studies__media{
        width: 100%;
        height: 100%;
        object-fit: cover;
        
    }
    .case-studies__title{
        margin: 10px 0;
        font-size: 1.3em;
    }
    .case-studies__text{
        width: 60%;
        font-size: 0.9em;
        font-weight: 500;
    }
}




.feature{
    width: 100%;
    height: calc(width);
    .feature__big-title{
        display: block;
        margin: 100px auto;
        width: 90%;
        font-size: 2.7rem;
        font-weight: 700;
    }
    .feature__container{
        width: 90%;
        height: 100%;
        margin: 0 auto;
        display: flex;
        overflow: auto;
        scrollbar-width: thin;
        scroll-behavior: smooth;
    }
    .feature__card{
        width: 400px;
        height: 100%;
        margin-right: 50px;
    }
    .feature__logo{
        height: 50px;
        margin-bottom: 100px;
    }
    .feature__title{
        margin-bottom: 30px;
        font-size: 1.5rem;
        position: relative;
        &:before{
            height: 3px;
            position: absolute;
            content: '';
            top: -90px;
            left: 0;
            background-color: #252422;
            width: 20px;
        }
    }
    .feature__text{
        margin-bottom: 100px;
        font-size: 1.1rem;
        font-weight: 410;
        letter-spacing: 0.03rem;
    }
    .feature__link{
        color: #252422;
    }
}




.spotlight{
    width: 100%;
    height: 130vh;
    background-color: #252422;
    color: #f9cdcd;
    .spotlight__container{
        width: 90%;
        height: 100%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: start;
    }
    .spotlight__content{
        width: 50%;
        height: 100%;
        margin: 200px 0;
        margin-right: 80px;
    }
    .spotlight__link{
        display: inline-block;
        text-decoration: none;
        color: #f9cdcd;
        border: #f9cdcd 1px solid;
        border-radius: 20px;
        padding: 8px 30px;
        margin-top: 80px;
        font-size: 0.8rem;
        font-weight: 600;
        background-image: linear-gradient(to top, #f9cdcd 50%, #252422 50%);
        background-size: 100% 200%;
        &:hover{
            animation: button-animation-pink 0.25s 1 ease-in-out forwards;
            
        }
        &:not(hover){
            animation: button-animation-pink-off 0.25s 1 ease-in-out forwards;
        }
    }
    .spotlight__title{
        font-size: 6rem;
        font-weight: 700;
        line-height: 5.5rem;
    }
    .spotlight__text{
        margin-top: 20px;
        font-size: 1.2rem;
    }
    .spotlight__text--bold{
        margin-top: 20px;
        font-size: 1.2rem;
        font-weight: 600;
    }
    .spotlight__media-container{
        width: 50%;
        height: 100%;
        margin: 80px 0;
        overflow: hidden;
    }
    .spotlight__media{
        object-fit: cover;
    }
}



.featured-news{
    width: 100%;
    height: auto;
    padding: 50px 0;
    .featured-news__container{
        width: 90%;
        height: auto;
        margin: 0 auto;
    }
    .featured-news__header{
        width: 100%;
        height: 100px;
        margin: 50px 0;
        display: flex;
        justify-content: space-between;
        align-items: start;
    }
    .featured-news__header-title{
        font-size: 2.5rem;
        font-weight: 700;
        line-height: 2.7rem;
    }
    .featured-news__link{
        display: inline-block;
        text-decoration: none;
        color: #252422;
        border: #252422 1px solid;
        border-radius: 20px;
        padding: 8px 30px;
        margin-top: 40px;
        font-size: 0.7rem;
        font-weight: 600;
        background-image: linear-gradient(to top, #252422 50%, #f4f4f4 50%);
        background-size: 100% 200%;
        &:hover{
            animation: button-animation 0.25s 1 ease-in-out forwards;
            
        }
        &:not(hover){
            animation: button-animation-off 0.25s 1 ease-in-out forwards;
        }
    }
    .featured-news__card{
        border-top: #252422 solid 1px;
        padding: 20px 0;
        margin-bottom: 4vw;
        display: block;
        width: 100%;
        height: 21vw;
        text-decoration: none;
        color: #252422;
        text-transform: uppercase;
        display: flex;
        justify-content: space-between;
        &:hover .featured-news__title{
            text-decoration: #252422 underline 3px;
        }
        &:hover .featured-news__media{
            transform: scale(1.05);
            transition: all 0.2s ease-in-out;
        }
        &:hover .featured-news__button-arrow{
            right: -90px;
            transition: all 0.5s ease-in-out;
        }
        &:not(hover) .featured-news__media{
            transform: scale(1);
            transition: all 0.2s ease-in-out;
        }
    }
    .featured-news__media-container{
        width: 33%;
        height: 100%;
        overflow: hidden;
    }
    .featured-news__media{
        width: 100%;
        height: 100%;
        object-fit: cover;
        
    }
    .featured-news__content{
        width: 50%;
        height: 100%;
        margin-right: 250px;
        padding: 0 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .featured-news__title{
        font-size: 2.6vw;
        font-weight: 500;
    }
    .featured-news__text{
        font-size: 1vw;
        font-weight: 500;
    }
    .featured-news__text--bold{
        font-weight: 600;
    }
    .featured-news__button{
        width: 30px;
        position: relative;
        overflow: hidden;
    }
    .featured-news__button-arrow{
        position: absolute;
        top: 0;
        right: -30px;
        display: flex;
    }
    .featured-news__button-arrow-svg{
        margin-right: 30px;
        fill: #252422;
        width: 30px;
        height: 30px;
    }
}





.footer{
    width: 100%;
    .footer__container{
        width: 90%;
        margin: 0 auto;
    }
    .footer__content{
        background-color: #252422;
        color: #f4f4f4;
        padding: 120px 0;
    }
    .footer__content-header{
        display: flex;
        justify-content: space-between;
        align-items: start;
        flex-wrap: wrap;
    }
    .footer__content-header-logo{
        width: 50%;
    }
    .footer__content-header-logo-svg{
        fill: #f4f4f4;
        width: 70px;
    }
    .footer__content-header-text{
        width: 50%;
        padding-right: 200px;
        padding-left: 30px;
        font-size: 2rem;
        font-weight: 500;
        line-height: 2rem;
    }
    .footer__content-header-link{
        color: #f4f4f4;
    }
    .footer__content-footer{
        display: flex;
        justify-content: space-between;
        align-items: start;
        margin-top: 100px;
    }
    .footer__content-footer-sub{
        display: flex;
        flex-direction: column;
        margin-right: 50px;
    }
    .footer__content-footer-title{
        font-size: 1.2rem;
        position: relative;
        margin: 20px 25px;
        &:before{
            position: absolute;
            content: '';
            background-color: #f4f4f4;
            width: 13px;
            height: 13px;
            border-radius: 50%;
            top: 50%;
            left: -25px;
            transform: translateY(-50%);
        }
    }
    .footer__content-footer-links{
        color: #f4f4f4;
        text-decoration: none;
        margin-bottom: 2px;
        font-size: 1.1rem;
        &:hover{
            text-decoration: underline;
        }
    }
    .footer__content-footer-form{
        position: relative;
        margin-right: 50px;
    }
    .footer__content-footer-input{
        width: 560px;
        background-color: #252422;
        border: none;
        border-bottom: #f4f4f4 solid 1.5px;
        font-size: 1.1rem;
        padding-bottom: 15px;
        &::placeholder{
            color: #f4f4f4;
        }
        &:focus{
            outline: none;
        }
    }
    .footer__content-footer-button{
        background-color: #252422;
        border: none;
        position: absolute;
        top: 0;
        right: 5px;
    }
    .footer__content-footer-button-svg{
        fill: #f4f4f4;
        width: 20px;
        height: 20px;
    }
    .footer__copyright{
        width: 100%;
        background-color: rgb(28, 26, 26);
        color: #828282;
        padding-bottom: 10px;
    }
    .footer__copyright-container{
        width: 90%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px 0;
    }
    .footer__copyright-text{
        font-size: 0.7rem;
    }
}

@keyframes button-animation{
    0%{
        color: #252422;
        background-position: -100% 0;
    }
    100%{
        color: #f4f4f4f4;
        background-position: 0 100%;
    }
}
@keyframes button-animation-off{
    0%{
        color: #f4f4f4f4;
        background-position: 0 100%;
    }
    100%{
        color: #252422;
        background-position: -100% 0;
    }
}



@keyframes button-animation-pink{
    0%{
        color: #f9cdcd;
        background-position: -100% 0;
    }
    100%{
        color: #252422;
        background-position: 0 100%;
    }
}
@keyframes button-animation-pink-off{
    0%{
        color: #252422;
        background-position: 0 100%;
    }
    100%{
        color: #f9cdcd;
        background-position: -100% 0;
    }
}




