 background: inherit;\r\n        border: none;\r\n        color: #f4f4f4;\r\n        font-size: 1.3rem;\r\n        cursor: pointer;\r\n    }\r\n    .header__menu--black{\r\n        color: #252422;\r\n    }\r\n    .header__menu--pink{\r\n        color: #f9cdcd;\r\n    }\r\n}\r\n\r\n\r\n.accessibility{\r\n    box-sizing: content-box;\r\n    position: fixed;\r\n    bottom: 20px;\r\n    left: 20px;\r\n    z-index: 2;\r\n    background-color: #252422;\r\n    width: 30px;\r\n    height: 30px;\r\n    border-radius: 50%;\r\n    border: none;\r\n    padding: 8px;\r\n    cursor: pointer;\r\n    &:hover{\r\n        transform: scale(1.1);\r\n        transition: all 0.2s ease-in-out;\r\n    }\r\n    &:not(hover){\r\n        transform: scale(1);\r\n        transition: all 0.2s ease-in-out;\r\n    }\r\n    .accessibility__icon{\r\n        width: 100%;\r\n        height: 100%;\r\n        fill: #f4f4f4;\r\n    }\r\n}\r\n\r\n\r\n.hero{\r\n    width: 100%;\r\n    height: 100vh;\r\n    .hero__video{\r\n        width: 100%;\r\n        height: 100%;\r\n        object-fit: cover;\r\n    }\r\n}\r\n\r\n\r\n.awards{\r\n    width: 100%;\r\n    height: 70vh;\r\n    .awards__container{\r\n        border-bottom: #252422 solid 1px;\r\n        width: 90%;\r\n        height: 100%;\r\n        margin: 0 auto;\r\n        display: flex;\r\n        justify-content: space-around;\r\n        align-items: center;\r\n    }\r\n    .awards__link{\r\n        text-decoration: none;\r\n        color: #252422;\r\n        width: 33.3%;\r\n        text-align: center;\r\n        height: 40%;\r\n        &:hover{\r\n            .awards__logo{\r\n                transform: scale(1.05);\r\n                transition: all 0.3s ease-in-out;\r\n            }\r\n            .awards__title{\r\n                text-decoration: underline #252422 2px;\r\n            }\r\n        }\r\n        &:not(hover){\r\n            .awards__logo{\r\n                transform: scale(1);\r\n                transition: all 0.3s ease-in-out;\r